import React, { Component } from 'react';
import './Masthead.css';


export default class Element extends Component {

    componentDidMount() {
        require('../../js/external/twitter.js')();
    }

    render() {

	 return (
            <div className="Masthead">
                <div className="Masthead__item">
                    <span className="Masthead__label white">POINGS LIÉS</span>
                    <span className="Masthead__details">Un webdocumentaire réalisé par les étudiants de l'École de journalisme de Grenoble (EJDG)<a href="//twitter.com/EDJGrenoble" className="twitter-follow-button" data-show-count="false" data-lang="fr" data-dnt="true">Suivre</a></span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Sous la supervision de </span>
                    <span className="Masthead__details">Cédric Cousseau et Thibault Lefèvre</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Rédacteurs en chef</span>
                    <span className="Masthead__details">Martin Cauwel et Charlotte Machado</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Journalistes</span>
                    <span className="Masthead__details">Luc Chemla, Léo Corcos, Félix Gabory, Justine Guitton-Boussion, Alexandre Lepère, Alexandre Loc'h, Venantia Petillault et Xavier Ponroy</span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Graphisme</span>
                    <span className="Masthead__details">Louise Klethi, Céline Legay et Élise Martin</span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Codage</span>
                    <span className="Masthead__details">Julien Rolland</span>
                </div>
				<div className="Masthead__item">
                    <span className="Masthead__label white">Remerciements</span>
                    <span className="Masthead__details">Rénald Garrido, Kayser, Élodie et l'ensemble des boxeurs du CS Savinois ainsi que Robbie Davies Jr et son équipe</span>
                </div>
            </div>
        );
    }
}



